<template>
    <div>
        <NavBar @cleanTutorial="cleanTutorial" />
        <div class="evite" v-if="chrome && isDesktop()">
            <div class="evite-options">
                <div class="evite-action">
                    <h2 class="text-center mt-4">Horizontal Settings:</h2>
                    <p>Optimal Image resolution: 625x770 or 1250x1540</p>
                    <b-container fluid>
                        <div id="dettagli-evento">
                            <b-form-group id="invite-label-group" label="Invitation label:" label-for="invite-label"
                                description class="mb-3">
                                <b-form-textarea id="invite-label" v-model="inviteLabel"
                                    placeholder="Enter something..." rows="2" max-rows="3"></b-form-textarea>
                            </b-form-group>

                            <b-form-group id="invite-event-group" label="Event Name:" label-for="event-name" description
                                class="mb-3">
                                <b-form-textarea id="event-name" v-model="eventName" placeholder="Enter something..."
                                    rows="2" max-rows="3"></b-form-textarea>
                            </b-form-group>

                            <b-form-group id="temporal-group" label="Details:" label-for="temporal-details" description
                                class="mb-3">
                                <b-form-textarea id="temporal-details" v-model="temporalDetails"
                                    placeholder="Enter something..." rows="5" max-rows="6"></b-form-textarea>
                            </b-form-group>

                            <b-form-group id="footer-group" label="Footer / Address:" label-for="footer" description
                                class="mb-3">
                                <b-form-textarea id="footer" v-model="footer" placeholder="Enter something..." rows="2"
                                    max-rows="3"></b-form-textarea>
                            </b-form-group>
                        </div>
                    </b-container>
                    <b-button @click="saveEvite()" class="cta-primary mt-4" id="download-evite">Download Evite
                    </b-button>
                </div>
            </div>
            <div class="evite-configurator p-4" id="preview-evite">
                <div class="zoom-out">
                    <div class="vertical-evite" ref="capture">
                        <div class="evite-img" @click="$refs.eventImage.click()">
                            <img src="../assets/images/pokemon_evite_bg.jpg" class="img-holiday"/>
                        </div>
                        <div class="evite-body mb-5 flex-center">
                            <div class="evite-logo mb-4">
                                <img v-if="logoUrl" :src="logoUrl" />
                                <img src="../assets/images/logo_pokemon_evite.png" v-else />
                            </div>
                            <div class="evite-group-content">
                                <p class="inviteLabel" v-html="inviteLabel" @click="higlight('invite-label-group')"></p>
                                <h1 class="eventName" v-html="eventName" @click="higlight('invite-event-group')"></h1>
                                <p v-html="temporalDetails" @click="higlight('temporal-group')"></p>
                                <p class="evite-footer" v-html="footer" @click="higlight('footer-group')"></p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="disclaimer" v-else>
            <h1>Browser currently not supported. Please use Google Chrome on your computer.</h1>
        </div>
    </div>
</template>
<script>
import domtoimage from "dom-to-image-more"
import * as FileSaver from "file-saver"
import * as introJs from "intro.js/intro.js"
import NavBar from "../components/NavBar.vue"

export default {
    data() {
        return {
            url: "",
            logoUrl: null,
            inviteLabel: "Lorem ipsum dolor sit amet",
            eventName: "E-vite Title",
            temporalDetails: "Lorem ipsum dolor sit amet consectetur.<br>Lectus bibendum imperdiet consequat lectus non.",
            footer: "The invitation is strictly personal",
            chrome: false,
        }
    },
    components: {
        NavBar,
    },
    mounted() {
        this.url = require('@/assets/images/placeholder.jpg')
        if (this.fnBrowserDetect() === "chrome") {
            this.chrome = true
            if (!window.localStorage.getItem("tutorialSeen")) {
                this.startTutorial()
            }
        }
    },
    methods: {
        saveEvite() {
            const { capture } = this.$refs
            domtoimage.toBlob(capture).then((blob) => {
                FileSaver.saveAs(blob, "evite.jpg")
            })
        },
        onFileChange(e) {
            const file = e.target.files[0]
            this.url = URL.createObjectURL(file)
        },
        onLogoChange(e) {
            const file = e.target.files[0]
            this.logoUrl = URL.createObjectURL(file)
        },
        higlight(divName) {
            document.getElementById(divName).setAttribute("style", "border: 1px solid green;padding: 10px;")
            setTimeout(() => {
                document.getElementById(divName).setAttribute("style", "border: 0;padding: 0px;")
            }, 1500)
        },
        fnBrowserDetect() {
            const { userAgent } = navigator
            let browserName

            if (userAgent.match(/chrome|chromium|crios/i)) {
                browserName = "chrome"
            } else if (userAgent.match(/firefox|fxios/i)) {
                browserName = "firefox"
            } else if (userAgent.match(/safari/i)) {
                browserName = "safari"
            } else if (userAgent.match(/opr\//i)) {
                browserName = "opera"
            } else if (userAgent.match(/edg/i)) {
                browserName = "edge"
            } else {
                browserName = "No browser detection"
            }
            return browserName
        },
        isDesktop() {
            if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
                return false
            }
            return true
        },
        startTutorial() {
            setTimeout(
                () =>
                    introJs()
                        .setOptions({
                            exitOnOverlayClick: false,
                            disableInteraction: true,
                            showBullets: false,
                            steps: [
                                {
                                    intro: "Here is the brand new Fendi Digital Evite tool. You will find two sections on the screen: Settings and Preview.",
                                },
                                {
                                    element: document.querySelector("#evite-image-group"),
                                    intro: "With the 'choose file' button, you can select a file (the file shared by HQ) from your computer and use it as an Evite cover.",
                                },
                                {
                                    element: document.querySelector("#dettagli-evento"),
                                    intro: "You can fill out all the details of the event in this section.",
                                },
                                {
                                    element: document.querySelector("#preview-evite"),
                                    intro: "Check the preview section of the invitation to ensure it looks good.",
                                },
                                {
                                    element: document.querySelector("#download-evite"),
                                    intro: "Click 'Download Evite' once you have finished and are happy with your results",
                                },
                            ],
                        })
                        .start(),
                100
            )
            window.localStorage.setItem("tutorialSeen", true)
        },
        cleanTutorial() {
            window.localStorage.removeItem("tutorialSeen")
            this.startTutorial()
        },
    },
}
</script>
