import Vue from 'vue'
import App from './App.vue'
import introJs from "intro.js"
import VueRouter from 'vue-router'
// Import Bootstrap an BootstrapVue CSS files (order is important)
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'

// Make BootstrapVue available throughout your project
Vue.use(BootstrapVue)
// Optionally install the BootstrapVue icon components plugin
Vue.use(IconsPlugin)

Vue.config.productionTip = false
Vue.use(introJs)
Vue.use(VueRouter)
import "intro.js/minified/introjs.min.css";
import "./assets/base.css"

import Homepage from './views/HomePage.vue'
import Vertical from './views/VerticalEvite.vue'
import VerticalNoImg from './views/VerticalEviteNoImg.vue'
import Horizontal from './views/HorizontalEvite.vue'
import Holiday from './views/HolidayEvite.vue'
import HolidayFull from './views/HolidayFullEvite.vue'
import HolidayVertical from './views/HolidayVertical.vue'
import Pokemon from './views/PokemonEvite.vue'
import PokemonFull from './views/PokemonVerticalEvite.vue'


const routes = [
  { path: '/', component: Homepage },
  { path: '/vertical', component: Vertical },
  { path: '/verticalnoimg', component: VerticalNoImg },
  { path: '/horizontal', component: Horizontal },
  { path: '/holiday', component: Holiday },
  { path: '/holidayfull', component: HolidayFull },
  { path: '/holidayvertical', component: HolidayVertical },
  { path: '/pokemon', component: Pokemon },
  { path: '/pokemonfull', component: PokemonFull },
  { path: "*", component: Homepage }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
